<template>
	<div class="container">
		  <div style="margin-bottom: 15px;margin-top: -10px;font-size: 20px;">
			  <p></p>
		  </div>
	  <!--  -->
	  <div class="search-bar">
	    <el-input v-model="keyword" placeholder="" style="width: 300px;margin-right: 10px;"></el-input>
		<el-select v-model="section" placeholder=""  style="width: 200px;margin-right: 20px;">
		  <!-- <el-option label="" value="general"></el-option> -->
		  <!-- <el-option label="" value="ocean"></el-option>
		  <el-option label="" value="marine-life"></el-option> -->
		  <el-option label="" value="info"></el-option>
		  <el-option label="" value="news"></el-option>
		</el-select>
	    <el-button type="primary" @click="fetchData">&nbsp;</el-button>
	    <!-- <el-button type="primary" @click="showAddDialog">&nbsp;</el-button> -->
	  </div>
	
	   <div class="forum-table" style="min-height: 300px;">
	   <!--  -->
	      <el-table :data="filteredData" style="width: 100%">
	        <el-table-column label="" align="center" min-width="5%">
				  <template #default="{row}">
					<span>{{ getIndex(row) }}</span>
				  </template>
				</el-table-column>
	        <el-table-column prop="title" label="" min-width="65%"></el-table-column>
			<el-table-column prop="time" label=""  min-width="15%"></el-table-column>
			
	        <el-table-column label=""  min-width="15%">
	          <template #default="{row}">
				<div v-if='row.status==1'>
					<el-button type="text" @click="handlechange(row)"></el-button>
					<el-button type="text" @click="handleDelete(row)"></el-button>
				</div>
				<div v-if='row.status==2'>
					<el-button type="text" @click="handledo(row)"></el-button>
				</div>
				
	          </template>
	        </el-table-column>
	      </el-table>
		</div>
	
	  <!--  -->
	  <el-pagination
	    v-model:currentPage="currentPage"
	    :total="total"
	    :page-size="pageSize"
	    @current-change="handlePageChange"
	    layout="total, prev, pager, next, jumper"
	    style="margin-top: 20px;">
	  </el-pagination>
	
	  <!--  -->
	  <el-dialog
	    title="     "
	    v-model="addDialogVisible"
	    :close-on-click-modal="false"
	    :close-on-press-escape="false"
		  :modal="false"
	    width="700px"
	    center
		   :showClose="false"
		  >
		  
		  <h3 class="post-title">{{ title }}</h3>
		  <div style="min-height: 200px;">
			  <div v-html="content"></div>
		  </div>      
		  
	     <!--  -->
		<div class="dialog-footer" style="text-align: right;margin-top: 10px;">
		  <el-button @click="addDialogVisible=false"></el-button>
		  <!-- <el-button type="primary" @click="sendMessage"></el-button> -->
		</div>
	  </el-dialog>
	
	
	</div>
</template>

<script>
import axios from '@/api/index.js'
import { ElMessageBox, ElNotification } from 'element-plus'
export default {
  data() {
    return {
	  id:localStorage.getItem('id'),
	  section:'info',
	  //role:localStorage.getItem('role'),
      keyword: '', // 
      tableData: [], // 
	  filteredData: [], // 
      currentPage: 1, // 
      total: 0, // 
      pageSize: 10, // 
      addDialogVisible: false, // 
	  dialogVisible:false,
	  rowid:0,
	  title:'',
	  content:'',
    };
  },
  created() {
    this.fetchData();
  },
  beforeDestroy(){
	  
  },
  methods: {
	  handledo(row){
		  axios.post('/api/forum_disable',{'id':row.id,'section':this.section,'status':1},{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
		  	 // console.log(response);
		  	// 
		  	ElNotification({
		  	  title: '',
		  	  message: response.data.msg,
		  	  duration: 3000,
		  	});
		  	this.fetchData();
		  })
		  .catch(error => {
		  	// 
		  	console.error(':', error);
		  });
	  },
	  fetchData() {
		  axios.post('/api/forum_list',{'keyword':this.keyword,'section':this.section},{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
			  if(response.data.code==0){
				  this.tableData=[];
				    console.log(response);
				  // 
				  this.tableData = response.data.data;
				  this.total=this.tableData.length;
				  this.handleSearch();
				}else{
				  this.tableData=[];
				  this.total=0;
				  this.handleSearch();
				}
		  })
		  .catch(error => {
			// 
			console.error(':', error);
		  });
	  },
    handleSearch() {
      // 
      console.log(':', this.keyword);
	  // 
	  this.filteredData = this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);	
    },
    handlePageChange(page) {
      // 
      console.log(':', page);
	  this.currentPage = page;
	  this.handleSearch();
    },
	getIndex(row) {
	  // 
	  return this.tableData.indexOf(row) + 1;
	},
	handlechange(row){	
		this.addDialogVisible=true;
		this.title=row.title;
		this.content = row.content;
		console.log('content=',this.content)
	},
	handleDelete(row){
		axios.post('/api/forum_disable',{'id':row.id,'section':this.section,'status':2},{headers:{"Access-Control-Allow-Origin":"*"}})
		.then(response => {
			 // console.log(response);
			// 
			ElNotification({
			  title: '',
			  message: response.data.msg,
			  duration: 3000,
			});
			this.fetchData();
		})
		.catch(error => {
			// 
			console.error(':', error);
		});
	}
  }
};

</script>

