<template>
	<div class="post-page">
	    <el-form :model="postForm" label-width="80px">
	      <el-form-item label="" required>
	        <el-input v-model="postForm.title" placeholder=""  style="width: 1200px;"></el-input>
	      </el-form-item>
	      <el-form-item label="" required>
	        <el-select v-model="postForm.section" placeholder=""  style="width: 1200px;">
	          <!-- <el-option label="" value="general"></el-option> -->
	          <!-- <el-option v-if="role=='1'" label="" value="ocean"></el-option>
	          <el-option v-if="role=='1'" label="" value="marine-life"></el-option> -->
	          <el-option label="" value="info"></el-option>
	          <el-option label="" value="news"></el-option>
	        </el-select>
	      </el-form-item>
		  
	      <el-form-item label="" required style="">
	        <QuillEditor ref="myQuill" content-type="html" theme="snow" v-model="postForm.content" :options="editorOptions" style="height: 500px;width: 1200px;"></QuillEditor>
	      </el-form-item>
	      <el-form-item>
	        <el-button type="primary" @click="submitPost"></el-button>
	      </el-form-item>
	    </el-form>
	  </div>
</template>
 
<script>
import axios from '@/api/index.js';
//import {baseUrl} from '@/api/index.js';
import { ElMessageBox, ElNotification } from 'element-plus';
import { QuillEditor,Quill } from '@vueup/vue-quill';
import '@vueup/vue-quill/dist/vue-quill.snow.css';
 
//  import { ImageDrop } from 'quill-image-drop-module'
//  Quill.register('modules/imageDrop', ImageDrop)
  
//  import BlotFormatter from 'quill-blot-formatter'
//  Quill.register('modules/blotFormatter', BlotFormatter)

// const modules = {
//   name: 'blotFormatter', // 
//   module: BlotFormatter, // 
//   options: toolbar //
// }
 
// 
const toolbarOptions = [
//     -----['bold', 'italic', 'underline', 'strike']
['bold', 'italic', 'underline', 'strike'],
//   -----['blockquote', 'code-block']
['blockquote', 'code-block'],
// 12 -----[{ header: 1 }, { header: 2 }]
[{ header: 1 }, { header: 2 }],
// -----[{ list: 'ordered' }, { list: 'bullet' }]
[{ list: 'ordered' }, { list: 'bullet' }],
// /-----[{ script: 'sub' }, { script: 'super' }]
[{ script: 'sub' }, { script: 'super' }],
// -----[{ indent: '-1' }, { indent: '+1' }]
[{ indent: '-1' }, { indent: '+1' }],
// -----[{'direction': 'rtl'}]
[{ direction: 'rtl' }],
// -----[{ size: ['small', false, 'large', 'huge'] }]
[{ size: ['small', false, 'large', 'huge'] }],
// -----[{ header: [1, 2, 3, 4, 5, 6, false] }]
[{ header: [1, 2, 3, 4, 5, 6, false] }],
// -----[{ color: [] }, { background: [] }]
[{ color: [] }, { background: [] }],
// -----[{ font: [] }]
[{ font: [] }],
// -----[{ align: [] }]
[{ align: [] }],
// -----['clean']
['clean'],
// -----['link', 'image', 'video']
['link', 'image'],
// ['table'] // 
]

 
export default {
  components: {
    QuillEditor
  },
  data() {
      return {
		role:localStorage.getItem('role'),
        postForm: {
          title: '',
          section: '',
          content: '',
		  userid:localStorage.getItem('id'),
        },
        editorOptions: {
          placeholder: '',
          modules: {
            toolbar:{
				container:toolbarOptions,
				handlers: {'image': this.imageHandler},
				// imageDrop: true,
				//   // 
				// blotFormatter: {
				// 	// overlay: {
				// 	//    style: {
				// 	//        border: '2px solid red',
				// 	//    }
				// 	// },
				// 	toolbar: {
				// 	  mainClassName: 'blot-formatter__toolbar'
				// 	}
				// },
			}
          }
        }
      };
    },
	computed: {
		//console.log('baseUrl=',baseUrl);
		editor() {
			return this.$refs.myQuill.quill
		}
	},
    methods: {
		imageHandler: function () {
			//console.log('');
			var input = document.createElement('input');
			input.setAttribute('type', 'file');
			input.setAttribute('accept', 'image/png, image/gif, image/jpeg, image/bmp, image/x-icon');
			input.click();
			// 
			input.onchange = () => {
				var file = input.files[0];
				//console.log('file=',file);
				if (/^image\//.test(file.type)) {
					this.saveImage(file);
				} else {
					ElNotification({
					  title: '',
					  message: "",
					  duration: 3000,
					})
				}
			};
		},
		saveImage: function (file) {
			//console.log('file11=',file);
			var fd = new FormData();
			fd.append('file', file);

			console.log('fd=',fd)
			//let url = '/api/file_upload';
			axios.post('/api/file_upload',fd,{headers:{"Access-Control-Allow-Origin":"*",'Content-Type': 'multipart/form-data'}})
			.then(response => {
				this.insertImage(response.data.data);
				console.log(response.data.data);
			})
			.catch(error => {
				// 
				console.error(':', error);
			});
		},
		insertImage: function (url) {
			//console.log('22=',axios.baseURL)
			let quill = this.$refs.myQuill.getQuill();
			console.log('quill=', this.$refs.myQuill)
			let length = quill.getSelection().index;
			quill.insertEmbed(length, "image", url);
			quill.setSelection(length + 1);
		},
		
      submitPost() {
		  if(localStorage.getItem('id')==''||localStorage.getItem('id')==null){
			  ElNotification({
			    title: '',
			    message: "",
			    duration: 3000,
			  })
			  return;
		  }
		  this.postForm.content=this.$refs.myQuill.getHTML();
		  //console.log('postForm.content==',this.postForm.content);
		 axios.post('/api/add_papers',this.postForm,{headers:{"Access-Control-Allow-Origin":"*"}})
		 .then(response => {
			console.log(response);
			if(response.data.code==0){
				ElNotification({
				  title: '',
				  message: response.data.msg,
				  duration: 3000,
				})
			}else{
				ElNotification({
				  title: '',
				  message: response.data.msg,
				  duration: 3000,
				})
			}
		 })
		 .catch(error => {
			// 
			ElNotification({
			  title: '',
			  message: '',
			  duration: 3000,
			})
			//console.error(':', error);
		 }); 
      }
    }
  };
</script>
