<template>
	<div class="container">
		  <div style="margin-bottom: 15px;margin-top: -10px;font-size: 20px;">
			  <p></p>
		  </div>
	  <!--  -->
	  <div class="search-bar">
	    <el-input v-model="keyword" placeholder="" style="width: 200px;margin-right: 20px;"></el-input>
	    <el-button type="primary" @click="fetchData">&nbsp;</el-button>
	    <!-- <el-button type="primary" @click="showAddDialog">&nbsp;</el-button> -->
	  </div>
	
		<div class="forum-table" style="min-height: 300px;">
	      <!--  -->
	      <el-table :data="filteredData" style="width: 100%">
	        <el-table-column label="" align="center" min-width="5%">
				  <template #default="{row}">
					<span>{{ getIndex(row) }}</span>
				  </template>
				</el-table-column>
	        <el-table-column prop="username" label="" min-width="20%"></el-table-column>
			<el-table-column prop="full_name" label="" min-width="15%"></el-table-column>
			<el-table-column prop="phone_number" label="" min-width="15%"></el-table-column>
			
			<!-- <el-table-column v-if='row.role==2' label=""></el-table-column>
			<el-table-column v-if='row.role==3' label=""></el-table-column> -->
			
			<el-table-column label="" min-width="10%">
			  <template #default="{row}">
			    <span v-if='row.role==2' type="text"></span>
				<span v-if='row.role==3' type="text"></span>
			  </template>
			</el-table-column>
			
	        <el-table-column label="" min-width="15%">
	          <template #default="{row}">

				<div v-if='row.status==1'>
					<el-button v-if='row.role==2' type="text" @click="handledown(row)"></el-button>
					<el-button v-if='row.role==3' type="text" @click="handlechange(row)"></el-button>
					<el-button type="text" @click="handleDelete(row)"></el-button>
				</div>
	            <div v-if='row.status==2'>
	            	<el-button type="text" @click="handledo(row)"></el-button>
	            </div>
				
	          </template>
	        </el-table-column>
	      </el-table>
		</div>
	
	  <!--  -->
	  <el-pagination
	    v-model:currentPage="currentPage"
	    :total="total"
	    :page-size="pageSize"
	    @current-change="handlePageChange"
	    layout="total, prev, pager, next, jumper"
	    style="margin-top: 20px;">
	  </el-pagination>
	
	  <!--  -->
	  <el-dialog
	    title="    "
	    v-model="addDialogVisible"
	    :close-on-click-modal="false"
	    :close-on-press-escape="false"
		  :modal="false"
	    width="700px"
	    center
		   :showClose="false"
		  >
	    
			<!--  -->
			<div class="chat-content" ref="chatContent" height=600px width=650px; style="border: 1px solid skyblue;min-height: 600px;margin-bottom: 10px;border-radius: 4px;">
			  <div v-for="(message, index) in chatMessages" :key="index" class="message">{{ message }}
			  
			  </div>
			</div>
			
			<!--  -->
			<el-input v-model="inputMessage" placeholder="" class="input-message"></el-input>
		        
		  
	     <!--  -->
			<div class="dialog-footer" style="text-align: right;margin-top: 10px;">
			  <el-button @click="backto"></el-button>
			  <el-button type="primary" @click="sendMessage"></el-button>
			</div>
	  </el-dialog>
	
	
	</div>
</template>

<script>
import axios from '@/api/index.js'
import { ElMessageBox, ElNotification } from 'element-plus'
export default {
  data() {
    return {
	  id:localStorage.getItem('id'),
	  //role:localStorage.getItem('role'),
      keyword: '', // 
      tableData: [], // 
	  filteredData: [], // 
      currentPage: 1, // 
      total: 0, // 
      pageSize: 10, // 
      addDialogVisible: false, // 
	  dialogVisible:false,
	  inputMessage: '', // 
	  chatMessages: [], // 
	  timer:'',
	  rowid:0,
    };
  },
  created() {
    this.fetchData();
  },
  beforeDestroy(){
	  
  },
  methods: {
	  handledo(row){
		 axios.post('/api/user_disable',{'id':row.id,'status':1},{headers:{"Access-Control-Allow-Origin":"*"}})
		 .then(response => {
		 	 // console.log(response);
		 	// 
		 	ElNotification({
		 	  title: '',
		 	  message: response.data.msg,
		 	  duration: 3000,
		 	});
		 	this.fetchData();
		 })
		 .catch(error => {
		 	// 
		 	console.error(':', error);
		 }); 
	  },
	  fetchData() {
		  axios.post('/api/user_list',{'keyword':this.keyword},{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
			console.log(response);
			if(response.data.code==0){
			// 
				this.tableData = response.data.data;
				this.total=this.tableData.length;
				this.handleSearch();
		    }else{
			  this.tableData=[];
			  this.total=0;
			  this.handleSearch();
			}
		  })
		  .catch(error => {
			// 
			console.error(':', error);
		  });
	  },
    handleSearch() {
      // 
      console.log(':', this.keyword);
	  // 
	  this.filteredData = this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);	
    },
    handlePageChange(page) {
      // 
      console.log(':', page);
	  this.currentPage = page;
	  this.handleSearch();
    },
	getIndex(row) {
	  // 
	  return this.tableData.indexOf(row) + 1;
	},
	handlechange(row){	
		axios.post('/api/user_change',{'id':row.id},{headers:{"Access-Control-Allow-Origin":"*"}})
		.then(response => {
			//console.log(response);
			ElNotification({
			  title: '',
			  message: response.data.msg,
			  duration: 3000,
			});
			this.fetchData();
		})
		.catch(error => {
			// 
			console.error(':', error);
		});
	},
	handledown(row){
		axios.post('/api/user_change_down',{'id':row.id},{headers:{"Access-Control-Allow-Origin":"*"}})
		.then(response => {
			//console.log(response);
			ElNotification({
			  title: '',
			  message: response.data.msg,
			  duration: 3000,
			});
			this.fetchData();
		})
		.catch(error => {
			// 
			console.error(':', error);
		});
	},
	backto(){
		clearInterval(this.timer);
		this.addDialogVisible = false;
	},

	handleDelete(row){
		axios.post('/api/user_disable',{'id':row.id,'status':2},{headers:{"Access-Control-Allow-Origin":"*"}})
		.then(response => {
			 // console.log(response);
			// 
			ElNotification({
			  title: '',
			  message: response.data.msg,
			  duration: 3000,
			});
			this.fetchData();
		})
		.catch(error => {
			// 
			console.error(':', error);
		});
	}
  }
};

</script>

