<template>
	<div class="container">
		  <div style="margin-bottom: 15px;margin-top: -10px;font-size: 20px;">
			  <p></p>
		  </div>
	  <!--  -->
	  <div class="search-bar">
	    <el-input v-model="keyword" placeholder="" style="width: 300px;margin-right: 10px;"></el-input>
		<!-- <el-select v-model="section" placeholder=""  style="width: 200px;margin-right: 20px;">
		  <el-option label="" value="general"></el-option>
		  <el-option label="" value="ocean"></el-option>
		  <el-option label="" value="marine-life"></el-option>
		  <el-option label="" value="info"></el-option>
		  <el-option label="" value="news"></el-option>
		</el-select> -->
	    <el-button type="primary" @click="fetchData">&nbsp;</el-button>
	    <el-button type="primary" @click="dialogVisible=true">&nbsp;</el-button>
	  </div>
	
	   <div class="forum-table" style="min-height: 300px;">
	   <!--  -->
	      <el-table :data="filteredData" style="width: 100%">
	        <el-table-column label="" align="center" min-width="5%">
				  <template #default="{row}">
					<span>{{ getIndex(row) }}</span>
				  </template>
				</el-table-column>
	        <el-table-column prop="name" label="" min-width="15%"></el-table-column>
			<el-table-column prop="originalscientificname" label="" min-width="15%"></el-table-column>
			<el-table-column prop="kingdom" label="" min-width="15%"></el-table-column>
			<el-table-column prop="species" label="" min-width="15%"></el-table-column>
			<el-table-column prop="date_year" label="" min-width="5%"></el-table-column>
			<el-table-column prop="quantity" label="" min-width="5%"></el-table-column>
			<el-table-column prop="habitat_region" label="" min-width="15%"></el-table-column>
			<el-table-column prop="decimallongitude" label="" min-width="10%"></el-table-column>
			<el-table-column prop="decimallatitude" label=""  min-width="10%"></el-table-column>
			<el-table-column prop="time" label=""  min-width="15%"></el-table-column>
			
	        <el-table-column label=""  min-width="15%">
	          <template #default="{row}">
				<el-button type="text" @click="handlechange(row)"></el-button>
	            <el-button type="text" @click="handleDelete(row)"></el-button>
	          </template>
	        </el-table-column>
	      </el-table>
		</div>
	
	  <!--  -->
	  <el-pagination
	    v-model:currentPage="currentPage"
	    :total="total"
	    :page-size="pageSize"
	    @current-change="handlePageChange"
	    layout="total, prev, pager, next, jumper"
	    style="margin-top: 20px;">
	  </el-pagination>
	
	  <!--  -->
	  <el-dialog
	    title=""
	    v-model="addDialogVisible"
	    :close-on-click-modal="false"
	    :close-on-press-escape="false"
		  :modal="false"
	    width="700px"
	    center
		   :showClose="false"
		  >
		 <div class="forum-table" style="min-height: 300px;margin-top:5px;">
		   <el-form :model="formData" ref="form" label-width="100px" class="form">
			 
			 <el-form-item label="" prop="name">
		       <el-input type="text" v-model="formData.name"></el-input>
		     </el-form-item>
			 <el-form-item label="" prop="originalscientificname">
			   <el-input type="text" v-model="formData.originalscientificname"></el-input>
			 </el-form-item>
			 <el-form-item label="" prop="kingdom">
			   <el-input type="text" v-model="formData.kingdom"></el-input>
			 </el-form-item>
		     <el-form-item label="" prop="species">
		       <el-input type="text" v-model="formData.species"></el-input>
		     </el-form-item>
			 <el-form-item label="" prop="date_year">
			   <el-input type="text" v-model="formData.date_year"></el-input>
			 </el-form-item>
			 <el-form-item label="" prop="quantity">
			   <el-input type="text" v-model="formData.quantity"></el-input>
			 </el-form-item>
		     <el-form-item label="" prop="habitat_region">
		       <el-input type="text" v-model="formData.habitat_region"></el-input>
		     </el-form-item>
			 <el-form-item label="" prop="decimallongitude">
			   <el-input type="text" v-model="formData.decimallongitude"></el-input>
			 </el-form-item>
			 <el-form-item label="" prop="decimallatitude">
			   <el-input type="text" v-model="formData.decimallatitude"></el-input>
			 </el-form-item>
			 
		   </el-form>
		  </div>
		  
	     <!--  -->
		<div class="dialog-footer" style="text-align: right;margin-top: 10px;">
		  <el-button type="primary" @click="addDialogVisible=false"></el-button>
		  <el-button type="primary" @click="update"></el-button>
		</div>
	  </el-dialog>
	  
	  <!-- -->
	  <el-dialog
	    title=""
	    v-model="dialogVisible"
	    :close-on-click-modal="false"
	    :close-on-press-escape="false"
	  		  :modal="false"
	    width="700px"
	    center
	  		   :showClose="false"
	  		  >
	  		 <div class="forum-table" style="min-height: 300px;margin-top:5px;">
	  		   <el-form :model="formData1" ref="form1" label-width="100px" class="form">
	  		     
				 <el-form-item label="" prop="name">
	  		       <el-input type="text" v-model="formData1.name"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="originalscientificname">
	  		       <el-input type="text" v-model="formData1.originalscientificname"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="kingdom">
	  		       <el-input type="text" v-model="formData1.kingdom"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="species">
	  		       <el-input type="text" v-model="formData1.species"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="date_year">
	  		       <el-input type="text" v-model="formData1.date_year"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="quantity">
	  		       <el-input type="text" v-model="formData1.quantity"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="habitat_region">
	  		       <el-input type="text" v-model="formData1.habitat_region"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="decimallongitude">
	  		       <el-input type="text" v-model="formData1.decimallongitude"></el-input>
	  		     </el-form-item>
	  		     <el-form-item label="" prop="decimallatitude">
	  		       <el-input type="text" v-model="formData1.decimallatitude"></el-input>
	  		     </el-form-item>
				 
	  		   </el-form>
	  		  </div>
	  		  
	     <!--  -->
	  		<div class="dialog-footer" style="text-align: right;margin-top: 10px;">
	  		  <el-button type="primary" @click="dialogVisible=false"></el-button>
	  		  <el-button type="primary" @click="save"></el-button>
	  		</div>
	  </el-dialog>
	
	
	</div>
</template>

<script>
import axios from '@/api/index.js';
import { ElMessageBox, ElNotification } from 'element-plus';

export default {
  data() {
    return {
	  id:localStorage.getItem('id'),
	  //section:'general',
	  //role:localStorage.getItem('role'),
      keyword: '', // 
      tableData: [], // 
	  filteredData: [], // 
      currentPage: 1, // 
      total: 0, // 
      pageSize: 10, // 
      addDialogVisible: false, // 
	  dialogVisible:false,
	  rowid:0,
	  formData:{
		  rowid:0,
		  name:'',
		  species:'',
		  habitat_region:'',
		  quantity:0,
		  originalscientificname:'',
		  kingdom:'',
		  date_year:0,
		  decimallongitude:'',
		  decimallatitude:'',
		  section:'marine-life',
	  },
	  formData1:{
		  id:localStorage.getItem('id'),
		  name:'',
		  species:'',
		  habitat_region:'',
		  quantity:0,
		  originalscientificname:'',
		  kingdom:'',
		  date_year:0,
		  decimallongitude:'',
		  decimallatitude:'',
		  section:'marine-life',
	  }
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
	  save(){
		  if (!/^[0-9]*$/.test(this.formData1.quantity)){
			  ElNotification({
					title: '',
					message: '',
					duration: 3000,
			  });
			  return;
		  }
		  
		  if(this.formData1.name==''||this.formData1.species==''||this.formData1.habitat_region==''||this.formData1.quantity==''){
			  ElNotification({
				title: '',
				message: '~',
				duration: 3000,
			  });
			  return;
		  }
		  axios.post('/api/forum_add',this.formData1,{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
			//if(response.data.code==0){
				ElNotification({
					title: '',
					message: response.data.msg,
					duration: 3000,
				});
				this.dialogVisible=false;
				this.fetchData();
			//}
		  })
		  .catch(error => {
			// 
			console.error(':', error);
		  });
	  },
	  //
	  update(){
		  if(this.formData.name==''||this.formData.species==''||this.formData.habitat_region==''||this.formData.quantity==''){
			  ElNotification({
			    title: '',
			    message: '~',
			    duration: 3000,
			  });
			  return;
		  }
		  
		  axios.post('/api/forum_change',this.formData,{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
			    this.tableData=[],
			    console.log(response);
				// 
				this.addDialogVisible=false;
				this.fetchData();
		  })
		  .catch(error => {
		  			// 
		  			console.error(':', error);
		  });
	  },
	  
	  fetchData() {
		  axios.post('/api/forum_list',{'keyword':this.keyword,'section':'marine-life'},{headers:{"Access-Control-Allow-Origin":"*"}})
		  .then(response => {
			if(response.data.code==0){
			    this.tableData=[];
				console.log(response);
				// 
				this.tableData = response.data.data;
				this.total=this.tableData.length;
				this.handleSearch();
			}else{
			  this.tableData=[];
			  this.total=0;
			  this.handleSearch();
			}
			// }else{
			// 	this.tableData=[];
			// }
		  })
		  .catch(error => {
			// 
			console.error(':', error);
		  });
	  },
    handleSearch() {
      // 
      console.log(':', this.keyword);
	  // 
	  this.filteredData = this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);	
    },
    handlePageChange(page) {
      // 
      console.log(':', page);
	  this.currentPage = page;
	  this.handleSearch();
    },
	getIndex(row) {
	  // 
	  return this.tableData.indexOf(row) + 1;
	},
	handlechange(row){	
		this.addDialogVisible=true;
		this.formData.rowid=row.id;
		this.formData.name=row.name;
		this.formData.species = row.species;
		this.formData.habitat_region = row.habitat_region;
		this.formData.quantity = row.quantity;
		this.formData.originalscientificname = row.originalscientificname;
		this.formData.kingdom = row.kingdom;
		this.formData.date_year = row.date_year;
		this.formData.decimallongitude = row.decimallongitude;
		this.formData.decimallatitude = row.decimallatitude;
		
	},
	handleDelete(row){
		axios.post('/api/forum_disable',{'id':row.id,'section':'marine-life'},{headers:{"Access-Control-Allow-Origin":"*"}})
		.then(response => {
			 // console.log(response);
			// 
			ElNotification({
			  title: '',
			  message: response.data.msg,
			  duration: 3000,
			});
			this.fetchData();
		})
		.catch(error => {
			// 
			console.error(':', error);
		});
	}
  }
};

</script>

